/* Navbar CSS */

.Navbar {
  background-color: #ffffff;
  color: #fff;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Navbar ul {
  display: flex;
  gap: 2rem;
  list-style: none;
}

.Navbar ul li a {
  color: #000;
  text-decoration: none;
  font-size: 1.2rem;
}